<template>
  <div id="background">
    <div id="profile-box">
      <div class="title">
        <h1>用户信息</h1>
      </div>
      <hr>
      <table cellspacing="10">
        <tr>
          <td>ID</td>
          <tr>{{id}}</tr>
        </tr>
        <tr>
          <td>用户名</td>
          <td>{{username}}</td>
        </tr>
        <tr>
          <td>电子邮箱</td>
          <td><Input v-model="email"></Input></td>
        </tr>
        <tr>
          <td>电话号码</td>
          <td><Input v-model="phone"></Input></td>
        </tr>
        <tr>
          <Button type="success" size="large" :disabled="profile_box_submit_state" @click="profile_box_submit">提交修改</Button>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { getUserInfo, changeUserInfo } from '../../api/api'
export default {
  data: function() {
    return {
      id: '',
      username: '',
      email: '',
      phone: '',
    }
  },
  mounted: function() {
    let options = {
      username: window.localStorage.getItem('username'),
      id: window.localStorage.getItem('id'),
      token: window.localStorage.getItem('token'),
  }
  getUserInfo(options)
    .then(res=>{
      this.id = res.data.data.id
      this.username = res.data.data.username
      this.email = res.data.data.email
      this.phone = res.data.data.phone
  })
 },
 methods: {
  profile_box_submit: function() {
    let options = {
      id: this.id,
      username: this.username,
      email: this.email,
      phone: this.phone,
      token: window.localStorage.getItem('token'),
    }
    changeUserInfo(options)
      .then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.$Message.info('修改成功！')
            router.push('/home')
          } else {
            this.$Message.info('未知原因修改失败')
          }
        } else {
          this.$Message.info(`修改失败,状态码:${res.status}`)
        }
      })
  }
 },
 computed: {
  profile_box_submit_state: function() {
    if(this.email.length == 0 || this.phone.length == 0) {
      return true
    }
    return false
  }
 }

}
</script>
<style>
div {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
#profile-box {
  background-color: #FFFFFF;
  width: 50%;
  height: 600px;
  margin: 0 auto;
  margin-top: 30px;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
}
#profile-box table {
    margin: 0 auto;
    /*margin-left: 100px;*/
    margin-top: 20px;
  }
#profile-box tr {
  display: block;
  margin-bottom: 10px;
}

.title {
    margin-top: 30px;
}
</style>


